import React, { createContext, useState, useEffect, useContext } from 'react';
import axios from 'axios';
import io from 'socket.io-client';

const DataContext = createContext();

export const useData = () => useContext(DataContext);

export const DataProvider = ({ children }) => {
  // State for all data
  const [prediction, setPrediction] = useState(null);
  const [parameters, setParameters] = useState({
    schumann: null,
    tec: null,
    elf: null,
    gravity: null
  });
  const [riskZones, setRiskZones] = useState([]);
  const [recentAlerts, setRecentAlerts] = useState([]);
  const [historicalEvents, setHistoricalEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [socket, setSocket] = useState(null);

  // Connect to socket on component mount
  useEffect(() => {
    // Connect to the server
    const newSocket = io(process.env.REACT_APP_API_URL || 'http://localhost:5000');
    setSocket(newSocket);

    // Clean up on unmount
    return () => {
      newSocket.disconnect();
    };
  }, []);

  // Set up socket event listeners
  useEffect(() => {
    if (!socket) return;

    // Listen for prediction updates
    socket.on('prediction_update', (data) => {
      setPrediction(data);
    });

    // Listen for parameter updates
    socket.on('parameter_update', (data) => {
      setParameters(data);
    });

    // Clean up listeners on unmount
    return () => {
      socket.off('prediction_update');
      socket.off('parameter_update');
    };
  }, [socket]);

  // Fetch initial data
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        
        // Simulate API calls with mock data
        // In a real application, these would be actual API calls
        
        // Prediction data
        setPrediction({
          score: 0.72,
          confidence: 0.85,
          alertLevel: 'high',
          timestamp: new Date().toISOString(),
          contributingFactors: {
            schumannResonance: 0.15,
            totalElectronContent: 0.35,
            elfBurstActivity: 0.45,
            gravityField: 0.05
          }
        });
        
        // Parameter data
        setParameters({
          schumann: {
            labels: Array.from({ length: 24 }, (_, i) => `${i}:00`),
            datasets: [
              {
                label: 'Schumann Resonance (7.83 Hz)',
                data: Array.from({ length: 24 }, () => 7.83 + (Math.random() * 0.3 - 0.1)),
                borderColor: 'rgb(75, 192, 192)',
                backgroundColor: 'rgba(75, 192, 192, 0.5)',
                tension: 0.3,
              },
            ],
          },
          tec: {
            labels: Array.from({ length: 24 }, (_, i) => `${i}:00`),
            datasets: [
              {
                label: 'Total Electron Content (TECU)',
                data: Array.from({ length: 24 }, (_, i) => {
                  const timeOfDay = Math.sin((i - 6) * Math.PI / 12) * 10;
                  return Math.max(5, Math.round(20 + timeOfDay + (Math.random() * 4) - 2));
                }),
                borderColor: 'rgb(53, 162, 235)',
                backgroundColor: 'rgba(53, 162, 235, 0.5)',
                tension: 0.3,
              },
            ],
          },
          elf: {
            labels: Array.from({ length: 24 }, (_, i) => `${i}:00`),
            datasets: [
              {
                label: 'ELF Burst Activity (Relative Units)',
                data: Array.from({ length: 24 }, (_, i) => {
                  let value = 5 + (Math.random() * 3) - 1.5;
                  if (i >= 10 && i <= 14) {
                    value += 10 * Math.sin((i - 10) * Math.PI / 4);
                  }
                  return Math.max(1, Math.round(value));
                }),
                borderColor: 'rgb(255, 99, 132)',
                backgroundColor: 'rgba(255, 99, 132, 0.5)',
                tension: 0.3,
              },
            ],
          },
          gravity: {
            labels: Array.from({ length: 24 }, (_, i) => `${i}:00`),
            datasets: [
              {
                label: 'Gravity Field Variation (μGal)',
                data: Array.from({ length: 24 }, (_, i) => {
                  const variation = (Math.random() * 0.2) - 0.1;
                  const trend = i / 100;
                  return parseFloat((0.5 + variation + trend).toFixed(1));
                }),
                borderColor: 'rgb(255, 159, 64)',
                backgroundColor: 'rgba(255, 159, 64, 0.5)',
                tension: 0.3,
              },
            ],
          }
        });
        
        // Risk zones
        setRiskZones([
          { id: 1, lat: 37.7749, lng: -122.4194, risk: 0.8, radius: 50, location: 'San Francisco, CA' },
          { id: 2, lat: 34.0522, lng: -118.2437, risk: 0.6, radius: 40, location: 'Los Angeles, CA' },
          { id: 3, lat: 40.7128, lng: -74.0060, risk: 0.3, radius: 30, location: 'New York, NY' },
          { id: 4, lat: 38.9072, lng: 23.7362, risk: 0.9, radius: 60, location: 'Euboea, Greece' },
        ]);
        
        // Recent alerts
        setRecentAlerts([
          { region: 'Euboea, Greece', score: 0.92, timestamp: '2025-05-18T08:30:00Z', status: 'Verified' },
          { region: 'San Francisco, CA', score: 0.78, timestamp: '2025-05-22T14:15:00Z', status: 'Active' },
          { region: 'Los Angeles, CA', score: 0.65, timestamp: '2025-05-22T16:45:00Z', status: 'Active' },
          { region: 'Tokyo, Japan', score: 0.58, timestamp: '2025-05-21T22:10:00Z', status: 'Expired' },
        ]);
        
        // Historical events
        setHistoricalEvents([
          { date: '2025-04-15', location: 'Euboea, Greece', magnitude: 5.2, score: 0.88, prediction: true },
          { date: '2025-03-22', location: 'Northern California', magnitude: 4.7, score: 0.76, prediction: true },
          { date: '2025-02-18', location: 'Southern Japan', magnitude: 5.5, score: 0.82, prediction: true },
          { date: '2025-01-30', location: 'Central Italy', magnitude: 4.3, score: 0.65, prediction: true },
          { date: '2025-01-12', location: 'Mexico City Region', magnitude: 4.8, score: 0.35, prediction: false },
          { date: '2024-12-05', location: 'Western Turkey', magnitude: 5.1, score: 0.91, prediction: true },
          { date: '2024-11-20', location: 'Taiwan', magnitude: 4.6, score: 0.42, prediction: false },
        ]);
        
        setLoading(false);
        setError(null);
      } catch (err) {
        console.error('Error fetching data:', err);
        setError('Failed to load data. Please try again later.');
        setLoading(false);
      }
    };
    
    fetchData();
  }, []);

  // Fetch historical data with parameters
  const fetchHistoricalData = async (timeRange, selectedParameters) => {
    try {
      // Simulate API call with mock data
      // In a real application, this would be an actual API call
      
      const mockData = {
        labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'].slice(0, timeRange === '1y' ? 12 : timeRange === '6m' ? 6 : timeRange === '3m' ? 3 : timeRange === '1m' ? 1 : 1),
        datasets: []
      };
      
      if (selectedParameters.includes('schumann')) {
        mockData.datasets.push({
          label: 'Schumann Resonance',
          data: Array.from({ length: mockData.labels.length }, () => (Math.random() * 0.3) + 7.7),
          borderColor: 'rgb(75, 192, 192)',
          backgroundColor: 'rgba(75, 192, 192, 0.5)',
        });
      }
      
      if (selectedParameters.includes('tec')) {
        mockData.datasets.push({
          label: 'Total Electron Content',
          data: Array.from({ length: mockData.labels.length }, () => Math.floor(Math.random() * 10) + 15),
          borderColor: 'rgb(53, 162, 235)',
          backgroundColor: 'rgba(53, 162, 235, 0.5)',
        });
      }
      
      if (selectedParameters.includes('elf')) {
        mockData.datasets.push({
          label: 'ELF Burst Activity',
          data: Array.from({ length: mockData.labels.length }, () => Math.floor(Math.random() * 15) + 5),
          borderColor: 'rgb(255, 99, 132)',
          backgroundColor: 'rgba(255, 99, 132, 0.5)',
        });
      }
      
      if (selectedParameters.includes('gravity')) {
        mockData.datasets.push({
          label: 'Gravity Field',
          data: Array.from({ length: mockData.labels.length }, () => (Math.random() * 0.4) + 0.3),
          borderColor: 'rgb(255, 159, 64)',
          backgroundColor: 'rgba(255, 159, 64, 0.5)',
        });
      }
      
      return mockData;
    } catch (err) {
      console.error('Error fetching historical data:', err);
      throw new Error('Failed to load historical data');
    }
  };

  // Ask AI assistant
  const askAssistant = async (question) => {
    try {
      // Simulate API call with mock responses
      // In a real application, this would be an actual API call
      
      const responses = {
        'What is the SR-GEO-PoC theory?': 'The SR-GEO-PoC (Schumann Resonance Geophysical Earthquake Prediction Concept) theory proposes that multiple geophysical parameters, including Schumann Resonance variations, Total Electron Content anomalies, ELF burst activity, and gravity field fluctuations, can be correlated to predict seismic events with greater accuracy than single-parameter approaches. The theory suggests these parameters exhibit specific patterns 24-72 hours before significant earthquakes.',
        'How does the prediction algorithm work?': 'The SR-GEO-PoC prediction algorithm analyzes real-time data from multiple parameters, looking for specific correlation patterns that have historically preceded seismic events. It assigns different weights to each parameter based on their statistical significance and temporal sequence. The algorithm then calculates a probability score and confidence level for potential seismic activity within the next 48-72 hours in monitored regions.',
        'What do the current anomalies mean?': 'The current data shows significant anomalies in ELF burst activity (300% above normal) and TEC (20% above baseline), with moderate anomalies in Schumann Resonance (+0.22 Hz shift). This pattern matches historical pre-seismic signatures with 83% correlation, suggesting a 72% probability of seismic activity within 48-72 hours, particularly in the Euboea, Greece and San Francisco regions.',
        'Explain the Schumann Resonance parameter': 'Schumann Resonances are global electromagnetic resonances generated by lightning discharges in the cavity formed by the Earth\'s surface and the ionosphere. The fundamental frequency is approximately 7.83 Hz. The SR-GEO-PoC theory monitors deviations from this baseline, particularly frequency shifts of +0.1 to +0.3 Hz, which have been observed 12-36 hours before significant seismic events. These shifts may result from ionospheric perturbations related to pre-seismic processes.',
        'How accurate are the predictions?': 'The SR-GEO-PoC system has demonstrated an overall accuracy rate of 83% for predicting significant seismic events (M4.5+) within the 72-hour window, with a false positive rate of 17%. The system has successfully predicted 5 out of 6 significant events in the past 6 months. Prediction accuracy varies by region, with higher success rates in the Mediterranean and Pacific Ring of Fire regions.',
        'What regions are currently at risk?': 'Based on current parameter analysis, the highest risk regions are Euboea, Greece (90% risk) and San Francisco, CA (80% risk), both showing significant anomalies across multiple parameters. Los Angeles, CA shows moderate risk (60%), while New York, NY shows lower risk (30%). The system continuously monitors these and other regions for changing conditions.'
      };
      
      // Default response for questions not in the predefined list
      let response = 'I don\'t have specific information about that question. The SR-GEO-PoC theory focuses on correlating multiple geophysical parameters (Schumann Resonance, TEC, ELF activity, and gravity field variations) to predict seismic events 24-72 hours before they occur. Would you like to know more about how the prediction system works or the current risk assessment?';
      
      // Check if we have a predefined response
      Object.keys(responses).forEach(key => {
        if (question.toLowerCase().includes(key.toLowerCase()) || 
            key.toLowerCase().includes(question.toLowerCase())) {
          response = responses[key];
        }
      });
      
      // Simulate network delay
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      return response;
    } catch (err) {
      console.error('Error getting AI response:', err);
      throw new Error('Failed to get a response from the assistant');
    }
  };

  return (
    <DataContext.Provider
      value={{
        prediction,
        parameters,
        riskZones,
        recentAlerts,
        historicalEvents,
        loading,
        error,
        fetchHistoricalData,
        askAssistant
      }}
    >
      {children}
    </DataContext.Provider>
  );
};

export default DataContext;
