import React, { useState, useEffect } from 'react';
import { MapContainer, TileLayer, Circle, Popup, LayerGroup } from 'react-leaflet';
import { useData } from '../context/DataContext';

const MapView = () => {
  // Get data from context
  const { riskZones, historicalEvents, loading, error } = useData();
  
  // State for map filters
  const [riskFilter, setRiskFilter] = useState('all'); // all, high, medium, low
  const [showHistorical, setShowHistorical] = useState(false);
  
  // Filter risk zones based on selected filter
  const filteredRiskZones = riskZones.filter(zone => {
    if (riskFilter === 'all') return true;
    if (riskFilter === 'high' && zone.risk >= 0.7) return true;
    if (riskFilter === 'medium' && zone.risk >= 0.4 && zone.risk < 0.7) return true;
    if (riskFilter === 'low' && zone.risk < 0.4) return true;
    return false;
  });
  
  // Get color based on risk level
  const getRiskColor = (risk) => {
    if (risk >= 0.7) return '#ff0000';
    if (risk >= 0.4) return '#ff9800';
    return '#ffeb3b';
  };
  
  // Get opacity based on risk level
  const getRiskOpacity = (risk) => {
    return 0.2 + (risk * 0.3);
  };
  
  // Show loading state
  if (loading) {
    return (
      <div className="container text-center my-5">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <p className="mt-3">Loading map data...</p>
      </div>
    );
  }
  
  // Show error state
  if (error) {
    return (
      <div className="container my-5">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
        <button 
          className="btn btn-primary" 
          onClick={() => window.location.reload()}
        >
          Retry
        </button>
      </div>
    );
  }
  
  return (
    <div className="container">
      <h2>Risk Zone Map</h2>
      <p className="lead">Geographic visualization of current seismic risk predictions</p>
      
      <div className="row mb-3">
        <div className="col-md-6">
          <div className="btn-group" role="group" aria-label="Risk filter">
            <button 
              type="button" 
              className={`btn ${riskFilter === 'all' ? 'btn-primary' : 'btn-outline-primary'}`}
              onClick={() => setRiskFilter('all')}
            >
              All Zones
            </button>
            <button 
              type="button" 
              className={`btn ${riskFilter === 'high' ? 'btn-danger' : 'btn-outline-danger'}`}
              onClick={() => setRiskFilter('high')}
            >
              High Risk
            </button>
            <button 
              type="button" 
              className={`btn ${riskFilter === 'medium' ? 'btn-warning' : 'btn-outline-warning'}`}
              onClick={() => setRiskFilter('medium')}
            >
              Medium Risk
            </button>
            <button 
              type="button" 
              className={`btn ${riskFilter === 'low' ? 'btn-success' : 'btn-outline-success'}`}
              onClick={() => setRiskFilter('low')}
            >
              Low Risk
            </button>
          </div>
        </div>
        <div className="col-md-6 text-md-end">
          <div className="form-check form-switch d-inline-block">
            <input
              className="form-check-input"
              type="checkbox"
              id="showHistorical"
              checked={showHistorical}
              onChange={() => setShowHistorical(!showHistorical)}
            />
            <label className="form-check-label" htmlFor="showHistorical">
              Show Historical Events
            </label>
          </div>
        </div>
      </div>
      
      <div className="row">
        <div className="col-md-9">
          <div className="map-container" style={{ height: '600px' }}>
            <MapContainer 
              center={[20, 0]} 
              zoom={2} 
              style={{ height: '100%', width: '100%' }}
              scrollWheelZoom={true}
            >
              <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              />
              
              {/* Risk Zones */}
              <LayerGroup>
                {filteredRiskZones.map(zone => (
                  <Circle
                    key={zone.id}
                    center={[zone.lat, zone.lng]}
                    radius={zone.radius * 1000}
                    pathOptions={{
                      color: getRiskColor(zone.risk),
                      fillColor: getRiskColor(zone.risk),
                      fillOpacity: getRiskOpacity(zone.risk)
                    }}
                  >
                    <Popup>
                      <div>
                        <h6>{zone.location}</h6>
                        <p>Risk Level: <strong>{(zone.risk * 100).toFixed(0)}%</strong></p>
                        <p>Based on SR-GEO-PoC analysis of multiple parameters:</p>
                        <ul>
                          <li>Schumann Resonance: +0.22 Hz shift</li>
                          <li>TEC Anomaly: 18% above baseline</li>
                          <li>ELF Burst Activity: Elevated</li>
                          <li>Gravity Field: Minor variations</li>
                        </ul>
                        <p>Prediction window: 48-72 hours</p>
                      </div>
                    </Popup>
                  </Circle>
                ))}
              </LayerGroup>
              
              {/* Historical Events */}
              {showHistorical && (
                <LayerGroup>
                  {historicalEvents.map((event, index) => {
                    // Extract coordinates from location string (simplified)
                    const coords = {
                      'Euboea, Greece': [38.9072, 23.7362],
                      'Northern California': [40.7899, -124.0046],
                      'Southern Japan': [33.5904, 130.4178],
                      'Central Italy': [42.6043, 13.2904],
                      'Mexico City Region': [19.4326, -99.1332],
                      'Western Turkey': [38.4237, 27.1428],
                      'Taiwan': [23.6978, 120.9605]
                    };
                    
                    const [lat, lng] = coords[event.location] || [0, 0];
                    
                    return (
                      <Circle
                        key={index}
                        center={[lat, lng]}
                        radius={event.magnitude * 10000}
                        pathOptions={{
                          color: event.prediction ? '#4caf50' : '#f44336',
                          fillColor: event.prediction ? '#4caf50' : '#f44336',
                          fillOpacity: 0.3,
                          weight: 1
                        }}
                      >
                        <Popup>
                          <div>
                            <h6>{event.location}</h6>
                            <p>Date: {event.date}</p>
                            <p>Magnitude: M{event.magnitude.toFixed(1)}</p>
                            <p>Prediction Score: {(event.score * 100).toFixed(0)}%</p>
                            <p>
                              Status: <span style={{
                                color: event.prediction ? '#4caf50' : '#f44336',
                                fontWeight: 'bold'
                              }}>
                                {event.prediction ? 'Successfully Predicted' : 'Missed'}
                              </span>
                            </p>
                          </div>
                        </Popup>
                      </Circle>
                    );
                  })}
                </LayerGroup>
              )}
            </MapContainer>
          </div>
        </div>
        
        <div className="col-md-3">
          <div className="map-legend card">
            <div className="card-body">
              <h5 className="card-title">Map Legend</h5>
              
              <h6 className="mt-3">Risk Zones</h6>
              <div className="legend-item">
                <span className="legend-color" style={{ backgroundColor: '#ff0000' }}></span>
                <span>High Risk (70-100%)</span>
              </div>
              <div className="legend-item">
                <span className="legend-color" style={{ backgroundColor: '#ff9800' }}></span>
                <span>Medium Risk (40-69%)</span>
              </div>
              <div className="legend-item">
                <span className="legend-color" style={{ backgroundColor: '#ffeb3b' }}></span>
                <span>Low Risk (0-39%)</span>
              </div>
              
              {showHistorical && (
                <>
                  <h6 className="mt-3">Historical Events</h6>
                  <div className="legend-item">
                    <span className="legend-color" style={{ backgroundColor: '#4caf50' }}></span>
                    <span>Predicted Event</span>
                  </div>
                  <div className="legend-item">
                    <span className="legend-color" style={{ backgroundColor: '#f44336' }}></span>
                    <span>Missed Event</span>
                  </div>
                </>
              )}
              
              <div className="mt-4">
                <p><small>Circle size indicates magnitude or risk zone radius.</small></p>
                <p><small>Click on circles for detailed information.</small></p>
              </div>
            </div>
          </div>
          
          <div className="card mt-3">
            <div className="card-body">
              <h5 className="card-title">Current Status</h5>
              <p>The map shows {filteredRiskZones.length} active risk zones based on SR-GEO-PoC analysis.</p>
              <p>
                <strong>Highest risk:</strong> {
                  riskZones.length > 0 
                    ? `${riskZones.sort((a, b) => b.risk - a.risk)[0].location} (${(riskZones.sort((a, b) => b.risk - a.risk)[0].risk * 100).toFixed(0)}%)`
                    : 'None'
                }
              </p>
            </div>
          </div>
        </div>
      </div>
      
      <div className="attribution">
        <p>Based on SR-GEO-PoC Theory | DOI: <a href="https://doi.org/10.5281/zenodo.15380641" target="_blank" rel="noopener noreferrer">10.5281/zenodo.15380641</a> | <a href="https://zenodo.org/records/15477659" target="_blank" rel="noopener noreferrer">Zenodo Repository</a></p>
      </div>
    </div>
  );
};

export default MapView;
