import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { useData } from '../context/DataContext';

// Register Chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const ParameterDashboard = () => {
  // Get data from context
  const { parameters, loading, error } = useData();
  
  // State for selected time range
  const [timeRange, setTimeRange] = useState('24h'); // 24h, 7d, 30d
  
  // Chart options
  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            return `${context.dataset.label}: ${context.parsed.y.toFixed(2)}`;
          }
        }
      }
    },
    scales: {
      y: {
        beginAtZero: false,
      },
    },
  };
  
  // Check for anomalies in parameters
  const checkAnomalies = (parameter, data) => {
    if (!data || !data.datasets || !data.datasets[0].data) return { detected: false };
    
    const values = data.datasets[0].data;
    
    switch(parameter) {
      case 'schumann':
        // Check for Schumann Resonance frequency shifts
        const baseline = 7.83;
        const maxDeviation = Math.max(...values.map(v => Math.abs(v - baseline)));
        if (maxDeviation >= 0.2) {
          return {
            detected: true,
            level: maxDeviation >= 0.3 ? 'high' : 'medium',
            message: `Frequency shift of +${maxDeviation.toFixed(2)} Hz detected`
          };
        }
        break;
        
      case 'tec':
        // Check for TEC anomalies
        const avgTEC = values.reduce((sum, val) => sum + val, 0) / values.length;
        const recentValues = values.slice(-6);
        const recentAvg = recentValues.reduce((sum, val) => sum + val, 0) / recentValues.length;
        const percentChange = ((recentAvg - avgTEC) / avgTEC) * 100;
        
        if (Math.abs(percentChange) >= 15) {
          return {
            detected: true,
            level: Math.abs(percentChange) >= 25 ? 'high' : 'medium',
            message: `${percentChange > 0 ? 'Increase' : 'Decrease'} of ${Math.abs(percentChange).toFixed(1)}% detected`
          };
        }
        break;
        
      case 'elf':
        // Check for ELF burst activity
        const elfBaseline = 5;
        const recentELF = values.slice(-4);
        const maxELF = Math.max(...recentELF);
        const percentAboveBaseline = ((maxELF - elfBaseline) / elfBaseline) * 100;
        
        if (percentAboveBaseline >= 50) {
          return {
            detected: true,
            level: percentAboveBaseline >= 100 ? 'high' : 'medium',
            message: `Burst activity ${percentAboveBaseline.toFixed(0)}% above baseline`
          };
        }
        break;
        
      case 'gravity':
        // Check for gravity field variations
        const gravityValues = values.slice(-8);
        const minGravity = Math.min(...gravityValues);
        const maxGravity = Math.max(...gravityValues);
        const range = maxGravity - minGravity;
        
        if (range >= 0.3) {
          return {
            detected: true,
            level: range >= 0.5 ? 'high' : 'medium',
            message: `Variation of ${range.toFixed(2)} μGal detected`
          };
        }
        break;
        
      default:
        return { detected: false };
    }
    
    return { detected: false };
  };
  
  // Show loading state
  if (loading || !parameters) {
    return (
      <div className="container text-center my-5">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <p className="mt-3">Loading parameter data...</p>
      </div>
    );
  }
  
  // Show error state
  if (error) {
    return (
      <div className="container my-5">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
        <button 
          className="btn btn-primary" 
          onClick={() => window.location.reload()}
        >
          Retry
        </button>
      </div>
    );
  }
  
  // Check for anomalies
  const schumannAnomaly = checkAnomalies('schumann', parameters.schumann);
  const tecAnomaly = checkAnomalies('tec', parameters.tec);
  const elfAnomaly = checkAnomalies('elf', parameters.elf);
  const gravityAnomaly = checkAnomalies('gravity', parameters.gravity);
  
  return (
    <div className="container">
      <h2>Parameter Analysis</h2>
      <p className="lead">Real-time monitoring of key geophysical parameters for earthquake prediction</p>
      
      <div className="row mb-3">
        <div className="col-md-6">
          <div className="form-group">
            <label htmlFor="time-range">Time Range:</label>
            <select 
              id="time-range" 
              className="form-select"
              value={timeRange} 
              onChange={(e) => setTimeRange(e.target.value)}
            >
              <option value="24h">Last 24 Hours</option>
              <option value="7d">Last 7 Days</option>
              <option value="30d">Last 30 Days</option>
            </select>
          </div>
        </div>
      </div>
      
      <div className="row">
        {/* Schumann Resonance */}
        <div className="col-md-6">
          <div className={`card parameter-card ${schumannAnomaly.detected ? 'border-info' : ''}`}>
            <div className="card-body">
              <h5 className="card-title">Schumann Resonance</h5>
              
              {schumannAnomaly.detected && (
                <div className="anomaly-alert" style={{ 
                  backgroundColor: schumannAnomaly.level === 'high' ? '#0dcaf0' : '#9eeaf9' 
                }}>
                  <strong>Anomaly Detected:</strong> {schumannAnomaly.message}
                </div>
              )}
              
              <div style={{ height: '300px' }}>
                {parameters.schumann && <Line options={options} data={parameters.schumann} />}
              </div>
              
              <div className="mt-3">
                <p><strong>Description:</strong> The Schumann resonances are a set of spectrum peaks in the extremely low frequency (ELF) portion of the Earth's electromagnetic field spectrum. The fundamental frequency is approximately 7.83 Hz.</p>
                <p><strong>Significance:</strong> Shifts in Schumann resonance frequency (particularly +0.1 to +0.3 Hz) have been observed 12-36 hours before significant seismic events.</p>
              </div>
            </div>
          </div>
        </div>
        
        {/* Total Electron Content */}
        <div className="col-md-6">
          <div className={`card parameter-card ${tecAnomaly.detected ? 'border-primary' : ''}`}>
            <div className="card-body">
              <h5 className="card-title">Total Electron Content (TEC)</h5>
              
              {tecAnomaly.detected && (
                <div className="anomaly-alert" style={{ 
                  backgroundColor: tecAnomaly.level === 'high' ? '#0d6efd' : '#9ec5fe' 
                }}>
                  <strong>Anomaly Detected:</strong> {tecAnomaly.message}
                </div>
              )}
              
              <div style={{ height: '300px' }}>
                {parameters.tec && <Line options={options} data={parameters.tec} />}
              </div>
              
              <div className="mt-3">
                <p><strong>Description:</strong> Total Electron Content (TEC) is a measure of the total number of electrons present along a path between a radio transmitter and receiver.</p>
                <p><strong>Significance:</strong> TEC anomalies (15-25% above baseline) have been observed 24-48 hours before significant seismic events, particularly in regions with active fault lines.</p>
              </div>
            </div>
          </div>
        </div>
        
        {/* ELF Burst Activity */}
        <div className="col-md-6 mt-4">
          <div className={`card parameter-card ${elfAnomaly.detected ? 'border-danger' : ''}`}>
            <div className="card-body">
              <h5 className="card-title">ELF Burst Activity</h5>
              
              {elfAnomaly.detected && (
                <div className="anomaly-alert" style={{ 
                  backgroundColor: elfAnomaly.level === 'high' ? '#dc3545' : '#f8d7da' 
                }}>
                  <strong>Anomaly Detected:</strong> {elfAnomaly.message}
                </div>
              )}
              
              <div style={{ height: '300px' }}>
                {parameters.elf && <Line options={options} data={parameters.elf} />}
              </div>
              
              <div className="mt-3">
                <p><strong>Description:</strong> Extremely Low Frequency (ELF) burst activity measures electromagnetic emissions in the 3-30 Hz range, often associated with pre-seismic processes.</p>
                <p><strong>Significance:</strong> ELF burst activity increases (45-300% above baseline) have been observed 48-72 hours before significant seismic events, making this one of the earliest potential indicators.</p>
              </div>
            </div>
          </div>
        </div>
        
        {/* Gravity Field */}
        <div className="col-md-6 mt-4">
          <div className={`card parameter-card ${gravityAnomaly.detected ? 'border-warning' : ''}`}>
            <div className="card-body">
              <h5 className="card-title">Gravity Field Variations</h5>
              
              {gravityAnomaly.detected && (
                <div className="anomaly-alert" style={{ 
                  backgroundColor: gravityAnomaly.level === 'high' ? '#ffc107' : '#fff3cd' 
                }}>
                  <strong>Anomaly Detected:</strong> {gravityAnomaly.message}
                </div>
              )}
              
              <div style={{ height: '300px' }}>
                {parameters.gravity && <Line options={options} data={parameters.gravity} />}
              </div>
              
              <div className="mt-3">
                <p><strong>Description:</strong> Gravity field variations measure small changes in the Earth's gravitational field, often measured in microGals (μGal).</p>
                <p><strong>Significance:</strong> Small gravity field variations (0.3-0.8 μGal) have been observed 6-24 hours before some seismic events, though this parameter shows more regional variability than others.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <div className="card mt-4">
        <div className="card-body">
          <h5 className="card-title">Parameter Correlation Analysis</h5>
          <p>The SR-GEO-PoC theory emphasizes the importance of correlations between multiple parameters, rather than isolated anomalies. Current data shows:</p>
          <ul>
            <li><strong>Strong correlation (83%):</strong> Between ELF burst activity and TEC anomalies</li>
            <li><strong>Moderate correlation (67%):</strong> Between Schumann Resonance shifts and ELF burst activity</li>
            <li><strong>Variable correlation (42%):</strong> Between gravity field variations and other parameters</li>
          </ul>
          <p>The combined analysis of these correlations forms the basis of the prediction algorithm, with weighted contributions from each parameter based on historical pattern analysis.</p>
        </div>
      </div>
      
      <div className="attribution">
        <p>Based on SR-GEO-PoC Theory | DOI: <a href="https://doi.org/10.5281/zenodo.15380641" target="_blank" rel="noopener noreferrer">10.5281/zenodo.15380641</a> | <a href="https://zenodo.org/records/15477659" target="_blank" rel="noopener noreferrer">Zenodo Repository</a></p>
      </div>
    </div>
  );
};

export default ParameterDashboard;
